es, el orden social y las obligaciones, se convierten en una comprensión dentro de las actuaciones. En este estadío, la razón personal está vinculada con el orden social y se convierte en una obligación que su conciencia sea coherente con las obligaciones determinadas por la sociedad.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Nivel posconvencional"}},[_c('p',[_vm._v("Con sus dos estadíos; el primero llamado orientación hacia el contrato social, en el que se destaca la concepción y relevancia de los valores y derechos humanos para garantizar un contrato social. La convicción, la convivencia y la vida social, toman una relevancia amplia. El segundo estadío es la orientación sobre los derechos universales, que es el nivel más elevado de los principios morales, en el que, desde el ideal, el comportamiento y el pensamiento van en una misma línea de coherencia y de humanidad, trascendiendo las leyes y la norma, por un desarrollo moral que se basa en razonamientos bajo principios morales universales.")])])])],1),_vm._m(3)]),_c('div',{staticClass:"bg-full-width border-top actividad bg-color-actividad"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('div',{attrs:{"id":"Actividad"}},[_c('Actividad',{attrs:{"cuestionario":_vm.cuestionario}})],1)])]),_c('div',{staticClass:"bg-full-width border-top color-primario"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('h2',{attrs:{"data-aos":"fade-left"}},[_vm._v("MATERIAL COMPLEMENTARIO")]),_c('div',{staticClass:"row material-complementario"},[_c('div',{staticClass:"col-12 col-md-6 col-lg-7"},[_c('p',[_vm._v("Los invitamos a explorar el material complementario de este curso, en esta sección encontrará recursos que le permitirán profundizar y enriquecer su aprendizaje en los temas tratados en esta unidad.")]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/componentes/link.svg")}}),_c('a',{attrs:{"href":"https://doi.org/10.14393/OBv2n3.a2018-47444","target":"_bla